import { useAuth } from "../context/AuthContext";
import { db } from '../config/firebase';
import { ref, set, get } from 'firebase/database';
import { PredictionProps } from "../interfaces";
import Moment from 'react-moment';
import 'moment-timezone';
import { useMatches } from '../context/MatchesContext';
import React, { useState } from 'react';
import Select, { ActionMeta } from 'react-select';

const PredictionItem = (match: PredictionProps) => {
  const user = useAuth()?.user;

  const allMatches = useMatches();

  const [selectedHome, setSelectedHome] = useState<string>('');
  const [selectedAway, setSelectedAway] = useState<string>('');
  const [selections, setSelections] = useState({});

  // Define the type for options
  type OptionType = {
    value: string;
    label: string;
  };

  interface Selections {
    [key: string]: string; // Key is the match ID and value is the team code
  }

  // Function to generate image source
  const generateImageSrc = (selectedHome: string) => {
    if (selectedHome && selectedHome !== "NNB") {
      return `https://img.uefa.com/imgml/flags/50x50/${selectedHome}.png`;
    } else if (match.home !== "NNB") {
      return `https://img.uefa.com/imgml/flags/50x50/${match.home}.png`;
    } else {
      // If selectedHome is empty, return a default image or any fallback image you prefer
      return 'data:image/png;base64,';
    }
  };


  // Function to generate image source
  const generateImageSrcAway = (selectedAway: string) => {
    if (selectedAway && selectedAway !== "NNB") {
      return `https://img.uefa.com/imgml/flags/50x50/${selectedAway}.png`;
    } else if (match.away !== "NNB") {
      return `https://img.uefa.com/imgml/flags/50x50/${match.away}.png`;
    } else {
      // If selectedHome is empty, return a default image or any fallback image you prefer
      return 'data:image/png;base64,';
    }
  };

  // Function to get team code by name
  function getTeamCodeByName(teamName: string): Promise<string | null> {
    return new Promise((resolve, reject) => {
      try {
        const teamsRef = ref(db, 'teams');
        get(teamsRef).then((teamsSnapshot) => {
          if (teamsSnapshot.exists()) {
            const teams = teamsSnapshot.val();
            for (const code in teams) {
              if (teams.hasOwnProperty(code)) {
                if (teams[code].name === teamName) {
                  resolve(code);
                  return;
                }
              }
            }
          }
          resolve(null); // Team name not found
        }).catch((error) => {
          console.error('Error fetching team:', error);
          resolve(null);
        });
      } catch (error) {
        console.error('Error fetching team:', error);
        resolve(null);
      }
    });
  }


  const handleHomeChange = async (selectedOption: OptionType | null) => {
    try {
      if (!selectedOption) {
        console.log("No option selected");
        return; // Handle case where no option is selected
      }

      const selectedHomeName: string = selectedOption.label;
      let teamCode = '';
      const result = await getTeamCodeByName(selectedHomeName);
      teamCode = result !== null ? result : 'NNB';

      // Only update selections and check duplicates if it's "Achtste finales"
      if (match.round.toString() === "Achtste finales") {
        const newSelections = { ...selections, [`home-${match.id}`]: selectedHomeName };
        setSelections(newSelections);

        checkForDuplicates(newSelections);
      }

      // Construct the database reference for home prediction and home team prediction
      const homePredictionRef = ref(db, `predictions/${user?.uid}/${match.id}/homeNamePrediction`);
      const homeTeamPredictionRef = ref(db, `predictions/${user?.uid}/${match.id}/homeTeamPrediction`);

      // Update the prediction with the selected home name and home team code in the database
      await Promise.all([
        set(homePredictionRef, selectedHomeName),
        set(homeTeamPredictionRef, teamCode)
      ]);

      setSelectedHome(selectedHomeName);

    } catch (error) {
      console.error("Error updating prediction:", error);
      // Handle error if needed
    }
  };

  const handleChangeDummy = async (selectedOption: OptionType | null) => {
    try {
      if (!selectedOption) return; // Handle case where no option is selected

      const selectedHomeName: string = selectedOption.label;
      const teamCode = selectedOption.value;


      // Construct the database reference for home prediction and home team prediction
      const homePredictionRef = ref(db, `predictions/${user?.uid}/dummy/homeNamePrediction`);
      const homeTeamPredictionRef = ref(db, `predictions/${user?.uid}/dummy/homeTeamPrediction`);

      // Update the prediction with the selected home name and home team code in the database
      await Promise.all([
        set(homePredictionRef, selectedHomeName),
        set(homeTeamPredictionRef, teamCode)
      ]);

      setSelectedHome(selectedHomeName);


    } catch (error) {
      console.error("Error updating prediction:", error);
      // Handle error if needed
    }
  };

  const handleAwayChange = async (selectedOption: OptionType | null, actionMeta: ActionMeta<OptionType>) => {
    try {
      if (!selectedOption) return; // Handle case where no option is selected

      const selectedAwayName: string = selectedOption.label;
      let teamCode = '';
      const result = await getTeamCodeByName(selectedAwayName);
      teamCode = result !== null ? result : 'NNB';

      // Only update selections and check duplicates if it's "Achtste finales"
      if (match.round.toString() === "Achtste finales") {
        const newSelections = { ...selections, [`home-${match.id}`]: selectedAwayName };
        setSelections(newSelections);

        checkForDuplicates(newSelections);
      }

      // Construct the database reference for home prediction and home team prediction
      const awayPredictionRef = ref(db, `predictions/${user?.uid}/${match.id}/awayNamePrediction`);
      const awayTeamPredictionRef = ref(db, `predictions/${user?.uid}/${match.id}/awayTeamPrediction`);

      // Update the prediction with the selected home name and home team code in the database
      await Promise.all([
        set(awayPredictionRef, selectedAwayName),
        set(awayTeamPredictionRef, teamCode)
      ]);

      setSelectedAway(selectedAwayName);


    } catch (error) {
      console.error("Error updating prediction:", error);
      // Handle error if needed
    }
  };

  const handleMenuOpen = async () => {
    // handle menu open here
    const dummySelectedOption = { value: 'GER', label: 'Duitsland' };

    try {
      // Call the handleHomeChange function with dummy input
      await handleChangeDummy(dummySelectedOption);
    } catch (error) {
      console.error('Error:', error);
    }
  };

  // Ensure allMatches is not null before proceeding
  if (!allMatches) {
    return null; // Or handle the null case in a different way
  }
  // Filter allMatches to include only items where matchItem.group ends with the same character as match.homeName
  const filteredHomeMatches = allMatches.filter(matchItem => matchItem.group.endsWith(match.homeName.charAt(match.homeName.length - 1)) || match.homeName.startsWith('W'))

  // Filter unique home names and home values
  const uniqueHomeNamesAndValues = filteredHomeMatches.reduce(
    (acc, matchItem) => {
      if (
        matchItem.homeName.length >= 4 &&
        !matchItem.homeName.includes('/') &&
        !acc.names.includes(matchItem.homeName)
      ) {
        acc.names.push(matchItem.homeName);
        acc.values.push(matchItem.home);
      }
      return acc;
    },
    { names: [] as string[], values: [] as string[] }
  );

  // Filter matches based on the conditions for awayName
  const filteredAwayMatches = allMatches.filter(matchItem => {
    // Check if match.awayName contains a slash
    if (match.awayName.includes('/')) {
      // Split match.awayName by slash and iterate over each part
      const parts = match.awayName.split('/');
      return parts.some(part =>
        matchItem.group && matchItem.group.endsWith(part.charAt(part.length - 1))
      );
    } else {
      // Check if match.awayName meets the original condition
      return (
        (match.awayName.length < 4 || match.awayName.includes('/')) &&
        matchItem.group &&
        matchItem.group.endsWith(match.awayName.charAt(match.awayName.length - 1)) || match.awayName.startsWith('W')
      );
    }
  });

  const uniqueAwayNamesAndValues = filteredAwayMatches.reduce(
    (acc, matchItem) => {
      if (
        matchItem.awayName.length >= 4 &&
        !matchItem.awayName.includes('/') &&
        !acc.names.includes(matchItem.awayName)
      ) {
        acc.names.push(matchItem.awayName);
        acc.values.push(matchItem.away);
      }
      return acc;
    },
    { names: [] as string[], values: [] as string[] }
  );

  const homePredictionHandler = (eventHome: React.ChangeEvent<HTMLInputElement>) => {
    set(ref(db, `predictions/${user?.uid}/${match.id}/homePrediction`), eventHome.target.value);
  };

  const awayPredictionHandler = (eventAway: React.ChangeEvent<HTMLInputElement>) => {
    set(ref(db, `predictions/${user?.uid}/${match.id}/awayPrediction`), eventAway.target.value);
  };

  const isEditable = () => {
    let twoHours = 3600;  // One hour before
    let now = Math.floor(new Date().getTime() / 1000);
    return match.timestamp - now > twoHours;
  }

  const checkForDuplicates = (currentSelections: Selections) => {
    const ids = [
      "home-38", "home-39", "home-40", "home-41", "home-42", "home-43", "home-44",
      "away-38", "away-39", "away-40", "away-41", "away-42", "away-43", "away-44"
    ];

    // Fetch values or textContent from the DOM elements using their IDs
    const domSelectedValues = ids.map(id => {
      const element = document.getElementById(id);
      let content = null;

      if (element instanceof HTMLInputElement) {  // Check if it is an input element
        content = element.value;
      } else if (element) {  // For other elements, use textContent
        content = element.textContent;
      }

      return content ? content.trim() : null; // Trim content and check for null or empty strings
    }).filter((value): value is string => value !== null && value !== ''); // Ensure only non-empty values are in the array

    // Combine values from state and DOM for a comprehensive duplicate check
    const combinedValues = [...domSelectedValues, ...Object.values(currentSelections).map(v => v.trim())];  // Trim values from state too
    //console.log("Combined Values for Duplicate Check:", combinedValues);

    // Count occurrences of each value to detect duplicates
    const valueCounts: { [key: string]: number } = {};
    combinedValues.forEach(value => {
      if (value) {
        valueCounts[value] = (valueCounts[value] || 0) + 1;
      }
    });

    // Identify any values that occur more than once indicating duplicates
    const duplicates = Object.keys(valueCounts).filter(value => valueCounts[value] > 1);

    if (duplicates.length > 0) {
      const duplicateMessage = "Team is dubbel geselecteerd in 8e finale. Corrigeer dit voor: " + duplicates.join(", ");
      alert(duplicateMessage);
    }
  };


  const styles = {
    control: (base: any) => ({
      ...base,
      minWidth: 148, // Set a minimum width for the select control
      width: '100%', // This makes the control flexible within its container
    }),
    menu: (base: any) => ({
      ...base,
      width: '100%', // Ensures the dropdown matches the width of the control
    }),
    singleValue: (base: any) => ({
      ...base,
      overflow: 'hidden',
      minWidth: 144,
      whiteSpace: 'nowrap',
      textOverflow: 'ellipsis', // Adds ellipsis if the text overflows
    }),
  };

  return (
    <div className="py-4 px-8 bg-white shadow-lg rounded-lg my-2">
      <table className="w-full border-separate [border-spacing:0.75rem]">
        <tbody>
          <tr>
            <td className="w-10"><img className="w-12" style={{ minWidth: '30px' }} alt={selectedHome ? selectedHome : match.home} src={generateImageSrc(match.homeTeamPrediction ? match.homeTeamPrediction : match.home)} /></td>
            <td className="text-xl">
              {/* Render select input if not Groepsfase */}

              {/* Render select input if not Groepsfase */}
              {match.round.toString() !== "Groepsfase" ? (
                <>
                  {/* Render select input */}
                  {isEditable() && match.uid === user?.uid ? (
                    <Select
                      onMenuClose={handleMenuOpen} // Workaround to refresh values based on previous selections
                      id={`home-${match.game}`}
                      //value={{ value: selectedHome, label: selectedHome }}
                      value={{ label: match.homeNamePrediction ? match.homeNamePrediction : `Kies ${match.homeName}`, value: match.homeTeamPrediction ? match.homeTeamPrediction : match.home }}
                      onChange={handleHomeChange}
                      styles={styles}
                      options={[
                        // Map over uniqueHomeNamesAndValues to generate options, excluding selectedHome and match.homeTeamPrediction values
                        ...uniqueHomeNamesAndValues.values.map((home, index) => {
                          // Check if the round is "Achtste finales"
                          if (match.round.toString() === "Achtste finales") {
                            return {
                              value: home,
                              label: uniqueHomeNamesAndValues.names[index],
                            };
                          } else {

                            // Get the value from the previous select with a name like W40
                            let indexFromHomeName = match.homeName.slice(-2);
                            let selectElement = document.getElementById(`home-${indexFromHomeName}`) as HTMLSelectElement | null;
                            let selectElementAway = document.getElementById(`away-${indexFromHomeName}`) as HTMLSelectElement | null;

                            let selectedValue = '';
                            let selectedText = '';
                            let selectedValueAway = '';
                            let selectedTextAway = '';
                            // If the select element exists
                            if (selectElement) {
                              selectedValue = selectElement.value;
                              //selectedText = selectElement.options[0].textContent || '';
                              const singleValueElement = selectElement.querySelector('div[class$="-singleValue"]');
                              if (singleValueElement) {
                                selectedText = (singleValueElement as HTMLElement).innerText;
                                selectedValue = selectedText
                              }
                            }
                            // If the select element exists
                            if (selectElementAway) {
                              selectedValueAway = selectElementAway.value;
                              const singleValueElementAway = selectElementAway.querySelector('div[class$="-singleValue"]');
                              if (singleValueElementAway) {
                                selectedTextAway = (singleValueElementAway as HTMLElement).innerText;
                                selectedValueAway = selectedTextAway
                                //getTeamCodeByName(selectedTextAway).then((teamCode) => {
                                //  selectedValueAway = teamCode ?? '';
                                // });
                              }
                            }

                            if (index < 1 && selectedText !== '') {
                              // Return options as a single array
                              return [
                                {
                                  value: selectedValue,
                                  label: selectedText,
                                },
                                {
                                  value: selectedValueAway,
                                  label: selectedTextAway,
                                }
                              ];
                            }
                          }
                          // Return empty array if conditions are not met
                          return [];
                        })
                        // Flatten the array of options
                      ].flat()}
                    />
                  ) : (
                    <span>{match.homeNamePrediction ? match.homeNamePrediction : '--'}</span>

                  )}
                </>
              ) : (
                match.homeName
              )}
            </td>
            {match.round.toString() !== "Groepsfase" && (
              <td className="w-10">{match.homeName}</td>
            )}
            {match.round.toString() === "Groepsfase" && (
              <React.Fragment>
                <td className="text-right">
                  {isEditable() && match.uid === user?.uid && (
                    <input
                      type="number"
                      value={match.homePrediction! > -1 ? match.homePrediction : ''}
                      onChange={homePredictionHandler}
                      className="w-16 shadow appearance-none border rounded py-1 px-2 text-gray-700 leading-tight text-center outline-none"
                    />
                  )}
                  {(!isEditable() || match.uid !== user?.uid) && (
                    <span className="border p-2 rounded-md bg-gray-100">{match.homePrediction! > -1 ? match.homePrediction : '-'}</span>
                  )}
                </td>
              </React.Fragment>
            )}
            <td className="text-right">
              <>{match.homeScore! > -1 ? match.homeScore : <span className="text-gray-300">--</span>}</>
            </td>
            {match.round.toString() === "Groepsfase" && (
            <td rowSpan={2} className="text-right w-10">
              {match.points! > -1 ? <div className="text-xl text-center rounded-full text-amber-900 bg-amber-300 p-1">{match.points}</div> : <span className="text-gray-300">--</span>}
            </td>
            )}
          </tr>
          <tr>
            <td className="w-10"><img className="w-12" style={{ minWidth: '30px' }} alt={selectedAway ? selectedAway : match.away} src={generateImageSrcAway(match.awayTeamPrediction ? match.awayTeamPrediction : match.away)} /></td>
            <td className="text-xl">
              {/* Render select input if match.awayName length is less than 4 and allMatches is not null */}
              {match.round.toString() !== "Groepsfase" ? (
                <>
                  {/* Render select input */}
                  {isEditable() && match.uid === user?.uid ? (
                    <div>
                      <Select
                        id={`away-${match.game}`}
                        styles={styles}
                        onMenuClose={handleMenuOpen}
                        //value={{ value: selectedHome, label: selectedHome }}
                        value={{ label: match.awayNamePrediction ? match.awayNamePrediction : `Kies ${match.awayName}`, value: match.awayTeamPrediction ? match.awayTeamPrediction : match.away }}
                        onChange={handleAwayChange}
                        options={[
                          // Map over uniqueHomeNamesAndValues to generate options, excluding selectedHome and match.homeTeamPrediction values
                          ...uniqueAwayNamesAndValues.values.map((away, index) => {
                            // Check if the round is "Achtste finales"
                            if (match.round.toString() === "Achtste finales") {
                              return {
                                value: away,
                                label: uniqueAwayNamesAndValues.names[index],
                              };
                            } else {

                              // Get the value from the previous select with a name like W40
                              let indexFromAwayName = match.awayName.slice(-2);
                              let selectElement = document.getElementById(`home-${indexFromAwayName}`) as HTMLSelectElement | null;
                              let selectElementAway = document.getElementById(`away-${indexFromAwayName}`) as HTMLSelectElement | null;

                              let selectedValue = '';
                              let selectedText = '';
                              let selectedValueAway = '';
                              let selectedTextAway = '';
                              // If the select element exists
                              if (selectElement) {
                                selectedValue = selectElement.value;
                                //selectedText = selectElement.options[0].textContent || '';
                                const singleValueElement = selectElement.querySelector('div[class$="-singleValue"]');
                                if (singleValueElement) {
                                  selectedText = (singleValueElement as HTMLElement).innerText;
                                  selectedValue = selectedText;
                                  //getTeamCodeByName(selectedText).then((teamCode) => {
                                  //  selectedValue = teamCode ?? '';
                                  //});
                                }
                              }
                              // If the select element exists
                              if (selectElementAway) {
                                selectedValueAway = selectElementAway.value;
                                const singleValueElementAway = selectElementAway.querySelector('div[class$="-singleValue"]');
                                if (singleValueElementAway) {
                                  selectedTextAway = (singleValueElementAway as HTMLElement).innerText;
                                  selectedValueAway = selectedTextAway;
                                  //getTeamCodeByName(selectedTextAway).then((teamCode) => {
                                  //selectedValueAway = teamCode ?? '';
                                  //});
                                }
                              }

                              if (index < 1 && selectedText !== '') {
                                // Return options as a single array
                                return [
                                  {
                                    value: selectedValue,
                                    label: selectedText,
                                  },
                                  {
                                    value: selectedValueAway,
                                    label: selectedTextAway,
                                  }
                                ];
                              }
                            }
                            // Return empty array if conditions are not met
                            return [];
                          })
                          // Flatten the array of options
                        ].flat()}
                      />
                    </div>
                  ) : (

                    <span>{match.awayNamePrediction ? match.awayNamePrediction : '--'}</span>
                  )}
                </>
              ) : (
                <span>{match.awayName}</span>
              )}
            </td>
            {match.round.toString() !== "Groepsfase" && (
              <td className="w-10">{match.awayName}</td>
            )}
            {match.round.toString() === "Groepsfase" && (
              <React.Fragment>
                <td className="text-right">
                  {isEditable() && match.uid === user?.uid && (
                    <input
                      type="number"
                      value={match.awayPrediction! > -1 ? match.awayPrediction : ''}
                      onChange={awayPredictionHandler}
                      className="w-16 shadow appearance-none border rounded py-1 px-2 text-gray-700 leading-tight text-center outline-none"
                    />
                  )}
                  {(!isEditable() || match.uid !== user?.uid) && (
                    <span className="border p-2 rounded-md bg-gray-100">{match.awayPrediction! > -1 ? match.awayPrediction : '-'}</span>
                  )}
                </td>
              </React.Fragment>
            )}
            <td className="text-right">
              <>{match.awayScore! > -1 ? match.awayScore : <span className="text-gray-300">--</span>}</>
            </td>
          </tr>
          <tr>
            <td colSpan={5} className="text-xs text-gray-700">
              <>{match.group! !== "N/A" ? match.group : match.round} </>
              · {match.location} · <Moment format="HH:mm">{match.date}</Moment>
            </td>
          </tr>
        </tbody>
      </table>
    </div >
  )
}

export default PredictionItem;